import React,{Component} from 'react';
import $ from 'jquery';

class Manipulator extends Component{
    constructor(props){
        super(props); 

        this.state = {
            originalList : "",
            manipulatedList : "",
        };

        this.originalListArray = [];
        this.manipulatedlistArray = [];

        this.notSelected = 'btn btn-info';
        this.isSelected = 'btn btn-danger';


        this.options = [];
        this.options.push({id:"1",selected:false,button:<button id='button1' key="1" type='button' className={this.notSelected} onClick={() => this.updateButtonProperties("1")}>DAT TO CSV</button>});
        this.options.push({id:"2",selected:false,button:<button id='button2' key="2" type='button' className={this.notSelected} onClick={() => this.updateButtonProperties("2")}>CSV TO DAT</button>});
        //Add option here
    }

    convertDatToCSV(){
        var newListString = "";

        var invoice_description_headers = "Trans,Program,Seq,Inv,Date,Start_date,End_Date";
        var orders_headers = "Trans,Program,Seq,Inv,Order,Date,Prov,Prog_var,Member,Rbc_id,Price,Fed_tax,Prov_tax";
        var total_headers = "Trans,Program,Seq,Inv Total,Federal Tax, Provincial Tax,Total";

        var selectedOption = this.options.filter(selectedOption => selectedOption.id === "1" && selectedOption.selected === true);
        var newList = [];
        if(selectedOption.length!==0){
            for(var run=0;run<this.manipulatedlistArray.length;run++){
                var value = this.manipulatedlistArray[run];
                //var length = value.length;
                
                switch(run){
                case 0:
                    var transaction_type = value.substring(0,1);
                    var program_source = value.substring(1,3);
                    var sequence_number = value.substring(3,9);
                    var invoice_number = value.substring(9,24);
                    var date = value.substring(24,32);
                    var start_date = value.substring(32,40);
                    var end_date = value.substring(40,48);
                    value = transaction_type + "," + program_source + "," + sequence_number + "," + invoice_number + "," + date + "," + "," + start_date + "," + end_date;
                    value = invoice_description_headers + "\r\n" + value + "\r\n\r\n" + orders_headers;
                    break;
                case (this.manipulatedlistArray.length-1):
                    var transaction_type = value.substring(0,1);
                    var program_source = value.substring(1,3);
                    var sequence_number = value.substring(3,9);
                    var total = value.substring(9,20);
                    var fed_tax = value.substring(20,31);
                    var prov_tax = value.substring(31,42);
                    var total_pages = value.substring(42,46);
                    value = transaction_type + "," + program_source + "," + sequence_number + "," + invoice_number + "," + total + "," + fed_tax + "," + prov_tax + "," + total_pages;
                    value = "\r\n" + total_headers + "\r\n" + value;
                    break;
                default:
                    var transaction_type = value.substring(0,1);
                    var program_source = value.substring(1,3);
                    var sequence_number = value.substring(3,9);
                    var invoice_number = value.substring(9,24);
                    var order_number = value.substring(24,36);
                    var date = value.substring(36,44);
                    var province = value.substring(44,46);
                    var program_var = value.substring(46,58);
                    var member_id = value.substring(58,67);
                    var rbc_id = value.substring(67,73);
                    var current_price = value.substring(73,81);
                    var federal_tax = value.substring(81,89);
                    var provincial_tax = value.substring(89,97);
                    value = transaction_type + "," + program_source + "," + sequence_number + "," + invoice_number + "," + order_number + "," + date + "," + province;
                    value = value + "," + program_var + "," + member_id + "," + rbc_id + "," + current_price + "," + federal_tax + "," + provincial_tax;
                    break;
                }

                newList.push(value);
                //newListString = newListString+length+"\r\n"+value+"\r\n";
                newListString = newListString+value+"\r\n";
            }
            this.manipulatedlistArray = newList;
            this.setState({
                manipulatedList : newListString,
            });
        }
    }

    convertCSVToDat(){
        var newListString = "";
        var selectedOption = this.options.filter(selectedOption => selectedOption.id === "2" && selectedOption.selected === true);
        var newList = [];
        if(selectedOption.length!==0){
            for(var run=0;run<this.manipulatedlistArray.length;run++){
                var value = this.manipulatedlistArray[run].replace(/,/g, '');
                //var length = value.length;
                newList.push(value);
                //newListString = newListString+length+"\r\n"+value+"\r\n";
                newListString = newListString+value+"\r\n";
            }
            this.manipulatedlistArray = newList;
            this.setState({
                manipulatedList : newListString,
            });
        }
    }

    //Add option function

    manipulateList(){
        this.manipulatedlistArray = this.originalListArray;
        this.convertCSVToDat();
        this.convertDatToCSV();
        //Call option function
    }

    resetManipulatedList(){
        this.manipulatedlistArray = [];
        this.setState({
            manipulatedList : "",
        });
    }

    reset(){
        this.originalListArray = [];
        this.setState({
            originalList : "",
        });
        this.resetManipulatedList();
    }

    demo(){
        var number = Math.floor((Math.random() * 100) + 1);
        this.originalListArray.push(number);
        var newString = "";
        if(this.state.originalList.length!==0)
            newString = this.state.originalList+"\n";
        this.setState({
            originalList : newString+number
        });
        if(this.options.filter(selectedOption => selectedOption.selected === true).length !== 0)
            this.manipulateList();
    }

    updateButtonProperties(thisOption){
        var option = this.options.find(option => option.id === thisOption);
        var optionIndex = this.options.findIndex(option => option.id === thisOption);
        if(option.selected){
            option.selected = false;
            $("#"+option.button.props.id).attr("class",this.notSelected);
        }else{
            option.selected = true;
            $("#"+option.button.props.id).attr("class",this.isSelected);
        }
        this.options[optionIndex] = option;
        this.setState(this.options);
        if(this.options.filter(selectedOption => selectedOption.selected === true).length !== 0)
            this.manipulateList();
        else
            this.resetManipulatedList();
    }

    renderOption(option){
        return <div className="row mb-4">
                    <div className="col text-center">
                        {option.button}
                    </div>
                </div>
    }

    renderOptions(){
        return (
            this.options.map(option => {return this.renderOption(option);
        }));
    }

    updateOriginalList(event) {
        event.preventDefault();
        this.setState({
            originalList : event.target.value,
        });
        if(this.state.originalList.length==0){
            var newArray = event.target.value.split("\n");
            this.originalListArray = newArray;
        }else{
            this.originalListArray = this.state.originalList.split("\n");
        }
        this.originalListArray = this.originalListArray.filter(function (el) {
            return el != "";
        });
        this.manipulateList();
    }

    render(){
        return <form className="row">
                    <div className="form-group col-lg-12 col-md-12 col-sm-12">
                        <h4 className="text-center col-lg-12 col-md-12 col-sm-12">Original List</h4>
                        <textarea value={this.state.originalList} onChange={(event) => this.updateOriginalList(event)} className="form-control col-lg-12 col-md-12 col-sm-12" rows="20"></textarea>
                    </div>
                    <div className="form-group col-lg-12 col-md-12 col-sm-12">
                        <h4 className="text-center col-lg-12 col-md-12 col-sm-12 mb-4">Options</h4>
                        {this.renderOptions()}
                        <div className="row mb-4"><div className="col text-center"><button type='button' className='btn btn-success' onClick={() => this.reset()}>RESET</button></div></div>
                    </div>
                    <div className="form-group col-lg-12 col-md-12 col-sm-12">
                        <h4 className="text-center col-lg-12 col-md-12 col-sm-12">Result</h4>
                        <textarea value={this.state.manipulatedList} className="form-control col-lg-12 col-md-12 col-sm-12" rows="20" readOnly></textarea>
                    </div>
                </form>
    }
}

export default Manipulator;